<template>
  <div class="setting-container" v-loading="loading">
    <div class="block-area">
      <p class="title">基本信息</p>
      <el-divider />
      <el-row>
        <el-col :span="12">
          <el-form label-width="100px" label-position="left" :model="userInfoForm" size="large">
            <el-form-item label="昵称">
              <el-input v-model="userInfoForm.nickname" placeholder="请输入您的昵称" clearable/>
            </el-form-item>
            <el-form-item label="行业">
              <el-select v-model="userInfoForm.industry" placeholder="请选择您的行业" filterable clearable no-match-text="无相关数据">
                <el-option
                  v-for="item in professionTypes"
                  :key="item.value"
                  :label="item.value"
                  :value="item.value"
                />
              </el-select>
            </el-form-item>
            <el-form-item label="公司">
              <el-input v-model="userInfoForm.company" placeholder="请输入公司名称" clearable/>
            </el-form-item>
            <el-form-item label="职业">
              <el-input v-model="userInfoForm.profession" placeholder="请输入职业" clearable/>
            </el-form-item>
            <el-form-item label="学历">
              <el-input v-model="userInfoForm.education" placeholder="请输入学历" clearable/>
            </el-form-item>
            <el-form-item label="个人简介">
              <el-input v-model="userInfoForm.remark" placeholder="请输入个人简介" type="textarea" show-word-limit maxlength="1000" rows="2" resize="none"/>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">提交</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12">
          <el-upload
            :action="action"
            :show-file-list="false"
            accept=".jpg,.jpeg,.png,.JPG,.JPEG"
            :on-success="handleUploadSuccess"
            :before-upload="beforeUpload"
            @mouseover="avatarMaskShow = true"
            @mouseout="avatarMaskShow = false"
          >
            <el-avatar :size="100" :src="userInfoForm.avatar || 'http://i.navigationline.cn/system/default/avatar.png'"/>
            <div class="avatar-mask" v-show="avatarMaskShow">编辑头像</div>
          </el-upload>
        </el-col>
      </el-row>
    </div>
    <div class="block-area">
      <p class="title">账号安全</p>
      <el-divider />
      <el-form label-width="100px" label-position="left" size="large">
        <el-form-item label="手机号">
          <span>{{phone}}</span>
          <el-button text type="primary" v-if="phone" @click="handleBindPhoneVisible()">更换手机号</el-button>
          <el-button text type="primary" v-else @click="handleBindPhoneVisible()">绑定手机号</el-button>
        </el-form-item>
        <el-form-item label="密码">
          <span>*******</span>
          <el-button text type="primary" @click="handleUpdatePasswordVisible()">更改密码</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
  <SimpleDialog v-model:visible="bindPhoneData.visible" :title="bindPhoneData.title" :footer="false">
    <BindPhoneForm @formResponse="handleBindPhoneRes" key="bindPhone-form"/>
  </SimpleDialog>
  <SimpleDialog v-model:visible="updatePasswordData.visible" :title="updatePasswordData.title" :footer="false">
    <UpdatePasswordForm @formResponse="handleUpdatePasswordRes" key="updatePassword-form"/>
  </SimpleDialog>
</template>

<script lang="ts" setup>
import {onBeforeUnmount, reactive, ref} from 'vue'
import {useSettingForm} from "@/views/user/useSettingForm";
import {useUserStore} from "@/store/modules/user";
import {fetchSaveUserInfo, fetchUserInfo} from "@/api/fetchs/user";
import {ElMessage} from "element-plus/es";
import SimpleDialog from '@/components/SimpleDialog/SimpleDialog.vue'
import BindPhoneForm from './BindPhoneForm.vue'
import UpdatePasswordForm from './UpdatePasswordForm.vue'
import {useBindPhoneForm} from "@/views/user/useBindPhoneForm";
import {useOffEmitFn, useOnEmitFn} from "@/utils/useEmitter";
import {useUpdatePasswordForm} from "@/views/user/useUpdatePasswordForm";
import env from "@/utils/env/env";

const {professionTypes} = useSettingForm()
const userStore = useUserStore()
const {bindPhoneData, handleBindPhoneVisible, handleBindPhoneRes} = useBindPhoneForm()
const {updatePasswordData, handleUpdatePasswordVisible, handleUpdatePasswordRes} = useUpdatePasswordForm()

const userInfoForm = reactive({
  nickname: '',
  industry: '',
  company: '',
  profession: '',
  education: '',
  remark: '',
  avatar: '',
})

const phone = ref<string>('')

const avatarMaskShow = ref<boolean>(false)

const action = `${env.publicApi}/api/v1/file/uploadAvatar`
const loading = ref<boolean>(false)

const onSubmit = () => {
  handleSaveUserInfo()
}

function handleUploadSuccess(res) {
  userInfoForm.avatar = res.data
  loading.value = false
}
function beforeUpload(file) {
  loading.value = true
  const isLt2M = file.size / 1024 / 1024 < 2
  if (!isLt2M) {
    // @ts-ignore
    ElMessage.error('图片不能超过2MB!')
  }
  return isLt2M
}

useOnEmitFn('handleUpdateUserInfo', handleGetUserInfo)

onBeforeUnmount(() => {
  useOffEmitFn('handleUpdateUserInfo')
})

/* ================================== fetchs ================================== */
async function handleGetUserInfo() {
  const {data} = await fetchUserInfo()

  userInfoForm.nickname = data.nickname
  userInfoForm.industry = data.industry
  userInfoForm.company = data.company
  userInfoForm.profession = data.profession
  userInfoForm.education = data.education
  userInfoForm.remark = data.remark
  userInfoForm.avatar = data.avatar
  phone.value = data.phone

  userStore.setUserInfo(data)
}

handleGetUserInfo()

async function handleSaveUserInfo() {
  const {data} = await fetchSaveUserInfo(userInfoForm)
  if(data) {
    ElMessage.success('保存成功')
    handleGetUserInfo()
  }
}
</script>

<style lang="scss" scoped>
.setting-container {
  background-color: #f4f4f4;
  padding: 20px 300px;
  min-height: calc(100vh - 50px);

  .block-area {
    background-color: #ffffff;
    border-radius: 6px;
    padding: 30px;
    margin-bottom: 20px;

    .title {
      font-size: 18px;
      font-weight: 700;
      color: #212930;
    }

    .el-select {
      width: 100%;
    }

    :deep(.el-upload) {
      position: absolute;
      right: 100px;
    }

    .el-avatar {
      position: relative;
    }

    .avatar-mask {
      position: absolute;
      background: rgba(0,0,0,0.2);
      color: #ffffff;
      left: 0;
      top: 0;
      width: 100px;
      height: 100px;
      border-radius: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
</style>
