<template>
  <ElForm
    ref="ruleFormRef"
    :model="form"
    label-position="right"
    label-width="0"
    class="login-basic-form"
  >
    <ElFormItem>
      <span style="color: #6c7d8f;font-size: 13px;">完善手机号后，可用手机号登录</span>
    </ElFormItem>
    <ElFormItem prop="phone" :rules="[{ required: true, message: '手机号码不能为空' }]">
      <el-input v-model="form.phone" placeholder="请输入新手机号码" clearable />
    </ElFormItem>
    <ElFormItem label="" prop="code" :rules="[{ required: true, message: '验证码不能为空' }]">
      <CodeButtonInput
        v-model="form.code"
        placeholder="请输入验证码"
        :sendCodeApi="handleSendCodeApi"
      />
    </ElFormItem>
    <div class="flex justify-end mt-10">
      <el-button type="primary" @click="handleSure">确定</el-button>
      <el-button @click="handleClose">取消</el-button>
    </div>
  </ElForm>

  <Verify
    mode="pop"
    captchaType="blockPuzzle"
    :imgSize="{width:'400px',height:'200px'}"
    ref="verify"
    @success="success"
  ></Verify>
</template>

<script setup lang="ts">
import {ref, reactive} from 'vue'
import {useEmit, useOnEmitFnForWait} from '@/utils/useEmitter'
import {ElMessage} from "element-plus";
import CodeButtonInput from '@/components/CodeButton/CodeButtonInput.vue'
import {fetchBindNewPhone, fetchSendCode} from "@/api/fetchs/user";
import Verify from '@/components/verifition/Verify.vue'

const verify = ref<any>(null)

const ruleFormRef = ref()
const form = reactive({
  phone: '',
  code: ''
})

const emit = defineEmits(['formResponse'])

function success(params) {
  useEmit('verfiySuccess', params)
}

async function handleSendCodeApi() {
  if (!form.phone) {
    // @ts-ignore
    ElMessage.warning('请先输入手机号')
    return false
  }

  // 显示图片验证码
  verify.value.show()

  const captchaParam = await useOnEmitFnForWait('verfiySuccess')

  await fetchSendCode({ address: form.phone, captchaVerification: captchaParam.captchaVerification, type: 2})

  return true
}

function handleSure() {
  ruleFormRef.value.validate(async valid => {
    if (valid) {
      const {data} = await fetchBindNewPhone(form)
      if(data) {
        ElMessage.success('手机号码修改成功')
        useEmit('handleUpdateUserInfo')
        emit('formResponse', true)
      }
    }
  })
}
function handleClose() {
  emit('formResponse', false)
}
</script>
