import {reactive} from 'vue'

export function useUpdatePasswordForm() {
  const updatePasswordData = reactive({
    visible: false,
    title: '请设置登录密码',
    type: 'add'
  })

  function handleUpdatePasswordVisible(type = 'add') {
    updatePasswordData.type = type
    updatePasswordData.visible = true
  }

  // 操作
  function handleUpdatePasswordRes(val) {
    if (val) {
      console.log('操作成功后...')
    }
    updatePasswordData.visible = false
  }

  return {
    updatePasswordData,
    handleUpdatePasswordVisible,
    handleUpdatePasswordRes
  }
}
