<template>
	<ElInput v-bind="$attrs" :size="size">
		<template #append>
			<CodeButton :size="size" :count="count" :value="state" :beforeStartFunc="sendCodeApi" />
		</template>
	</ElInput>
</template>
<script lang="ts">
	import { defineComponent, PropType } from 'vue'
	import CodeButton from './CodeButton.vue'
	import { useRuleFormItem } from '@/hooks/components/useFormItem'

	const props = {
		value: { type: String },
		size: { type: String, validator: v => ['large', 'default', 'small'].includes(v) },
		count: { type: Number, default: 60 },
		sendCodeApi: {
			type: Function as PropType<() => Promise<any>>,
			default: null
		}
	}

	export default defineComponent({
		name: 'CountDownInput',
		components: { CodeButton },
		inheritAttrs: false,
		props,
		setup(props) {
			const [state] = useRuleFormItem(props)

			return { state }
		}
	})
</script>
