import {reactive} from 'vue'

export function useBindPhoneForm() {
  const bindPhoneData = reactive({
    visible: false,
    title: '绑定新手机号',
    type: 'add'
  })

  function handleBindPhoneVisible(type = 'add') {
    bindPhoneData.type = type
    bindPhoneData.visible = true
  }

  // 操作
  function handleBindPhoneRes(val) {
    if (val) {
      console.log('操作成功后...')
    }
    bindPhoneData.visible = false
  }

  return {
    bindPhoneData,
    handleBindPhoneVisible,
    handleBindPhoneRes
  }
}
