<template>
  <ElForm
    ref="ruleFormRef"
    :model="form"
    label-position="right"
    label-width="0"
    class="login-basic-form"
  >
    <ElFormItem prop="oldPwd" :rules="[{ required: true, message: '旧密码不能为空' }]">
      <el-input v-model="form.oldPwd" type="password" placeholder="请输入旧密码" show-password />
    </ElFormItem>
    <ElFormItem prop="newPwd" :rules="getPasswordFormRegRule">
      <el-input v-model="form.newPwd" type="password" placeholder="请输入新密码" show-password />
    </ElFormItem>
    <ElFormItem prop="newPwd2" :rules="[{ required: true, message: '新密码不能为空' }]">
      <el-input v-model="form.newPwd2" type="password" placeholder="请再次输入新密码" show-password />
    </ElFormItem>
    <div class="flex justify-end mt-10">
      <el-button type="primary" @click="handleSure">确定</el-button>
      <el-button @click="handleClose">取消</el-button>
    </div>
  </ElForm>
</template>

<script setup lang="ts">
import {ref, reactive, computed} from 'vue'
import {ElMessage} from "element-plus";
import {fetchUpdatePassword} from "@/api/fetchs/user";

const ruleFormRef = ref()
const form = reactive({
  oldPwd: '',
  newPwd: '',
  newPwd2: '',
})

const getPasswordFormRegRule = computed(() => {
  return [
    {
      required: true,
      message: '新密码不能为空',
      trigger: 'change'
    },
    {
      message: '密码必须包含大小写字母、数字和特殊字符，长度8~16位',
      trigger: 'change',
      transform(value) {
        let pwdreg = /(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[\W_]).{8,16}/
        if (!pwdreg.test(value)) {
          return false
        } else {
          return value
        }
      }
    }
  ]
})

const emit = defineEmits(['formResponse'])

function handleSure() {
  ruleFormRef.value.validate(async valid => {
    if (valid) {
      if(form.newPwd !== form.newPwd2) {
        ElMessage.warning('两次新密码输入不一致')
        return
      }
      const {data} = await fetchUpdatePassword(form)
      if(data) {
        ElMessage.success('密码修改成功')
        emit('formResponse', true)
      }
    }
  })
}
function handleClose() {
  emit('formResponse', false)
}
</script>
